import "./orderList.css";
import { DataGrid, GridToolbar } from "@material-ui/data-grid";
import { DeleteOutline } from "@material-ui/icons";
import { orderRows } from "../../dummyData";
import { Link } from "react-router-dom";
import { useState } from "react";

export default function OrderList() {
  const [data, setData] = useState(orderRows);

  const handleDelete = (id) => {
    setData(data.filter((item) => item.id !== id));
  };

  const columns = [
    { field: "id", headerName: "ID", width: 90 },
    {
      field: "product",
      headerName: "Product",
      width: 200,
      renderCell: (params) => {
        return (
          <div className="order-list-item">
            <img className="order-list-img" src={params.row.img} alt="" />
            {params.row.name}
          </div>
        );
      },
    },
    { field: "customer", headerName: "Customer", width: 200 },
    { field: "quantity", headerName: "Quantity", width: 140 },
    {
      field: "price",
      headerName: "Price",
      width: 200,
    },
    {
      field: "action",
      headerName: "Action",
      width: 150,
      renderCell: (params) => {
        return (
          <>
            <Link to={"/order/" + params.row.id}>
              <button className="order-list-edit">Edit</button>
            </Link>
            <DeleteOutline
              className="order-list-delete"
              onClick={() => handleDelete(params.row.id)}
            />
          </>
        );
      },
    },
  ];

  return (
    <div className="order-list">
      <h2 className="order-header">Orders</h2>
      <DataGrid
        className="orders-table-list"
        rows={data}
        disableSelectionOnClick
        columns={columns}
        pageSize={10}
        components={{
          Toolbar: GridToolbar,
        }}
        filterModel={{
          items: [{ columnField: "", operatorValue: "", value: "" }],
        }}
        checkboxSelection
      />
    </div>
  );
}
